import React, { Fragment } from "react";
import "./category.css";

function Category(props) {
  return (
    <Fragment>
      <span
        onClick={(event) => props.onFilter(event, props.name)}
        className={
          "text-capitalize badge badge-pill badge-info primary-fb m-3 filter " +
          (props.filterSelected === props.name ? "secundary-fb" : "")
        }
      >
        {props.name}
      </span>
    </Fragment>
  );
}

export default Category;
