import Category from "../../../Category/components/category";
import ItemProject from "../../Item/components/item";
import Arrow from "../../../Assets/Images/arrow.svg";
import ModalContainer from "../../../Modal/containers/modal";
import Modal from "../../../Modal/components/modal";
import Carousel from "react-bootstrap/Carousel";

class ListProyects extends Component {
  state = {
    modalVisible: false,
    filter: "all",
    category: [
      {
        name: "all",
      },
      {
        name: "wordpress",
      },
      {
        name: "laravel",
      },
      {
        name: "react",
      },
    ],
    projects: [
      {
        name: "Aliant Payments",
        job: "HTML, CSS, JavaScript e Integración API",
        description:
          "Lorem ipsum dolor sit amet consectetur adipisicing elit. Saepe ratione, incidunt cum nihil a distinctio earum nobis. Cupiditate at placeat sunt consectetur dolorum, accusantium commodi. Voluptatum dolorem repudiandae sit in!",
        category: "react",
        client: "Shokworks INC",
        url: "https://int-cms.aliantpayments.com/",
        images: [
          {
            src: "Aliant/1.png",
          },
          {
            src: "Aliant/3.png",
          },
          {
            src: "Aliant/4.png",
          },
          {
            src: "Aliant/5.png",
          },
          {
            src: "Aliant/6.png",
          },
          {
            src: "Aliant/7.png",
          },
          {
            src: "Aliant/8.png",
          },
          {
            src: "Aliant/9.png",
          },
          {
            src: "Aliant/10.png",
          },
          {
            src: "Aliant/11.png",
          },
        ],
      },
      {
        name: "Industrial Automation Professionals",
        job: "HTML, CSS, Maquetación, Edición y Carga de contenido",
        description:
          "Grupo de profesionales especializados en soluciones de automatización con una experiencia laboral combinada de 32 años. Con servicios de Instrumentación industrial y VFD, Controladores lógicos programables (PLC), Control de supervisión y adquisición de datos (SCADA), Paneles de control,Soluciones de comunicación en red y Documentación",
        category: "wordpress",
        client: "Nelumbo Consultores",
        url: "http://iapengineering.com/",
        images: [
          {
            src: "IAP/1.png",
          },
          {
            src: "IAP/2.png",
          },
          {
            src: "IAP/3.png",
          },
          {
            src: "IAP/4.png",
          },
          {
            src: "IAP/5.png",
          },
          {
            src: "IAP/6.png",
          },
        ],
      },
      {
        name: "Viviendas y Valores S.A",
        job:
          "Colaboración con HTML, CSS, Maquetación, Edición y Carga de contenido",
        description:
          "Viviendas y Valores S.A. fue fundada el 20 de Diciembre de 1972. Empresa encargada de prestar servicios de Arriendos, Venta de Inmuebles y Avaluos.",
        category: "wordpress",
        client: "Nelumbo Consultores",
        url: "https://www.viviendasyvalores.com.co/",
        images: [
          {
            src: "VyV/1.png",
          },
          {
            src: "VyV/2.png",
          },
          {
            src: "VyV/3.png",
          },
          {
            src: "VyV/4.png",
          },
          {
            src: "VyV/5.png",
          },
          {
            src: "VyV/6.png",
          },
        ],
      },
      {
        name: "SISTGO",
        job:
          "Desarrollo API, Resolución de errores, Desarrollo de nuevas funciones",
        description:
          "Sistema integrado para gestionar, controlar y gestionar los procesos para pequeñas y grandes empresas que prestan servicios de personal. (Seguridad, construcción, salud, limpieza, etc.)",
        category: "laravel",
        client: "EmQu Technologies",
        url: "https://www.sistgo.com/",
        images: [
          {
            src: "Sistgo/1.png",
          },
        ],
      },
    ],
    dataSelected: {},
    index: 0,
    currentPage: 1,
    todosPerPage: 3,
  };

  onFilter = (event, filter_name) => {
    this.setState({ filter: filter_name, currentPage: 1 });
  };

  handleClick = (event) => {
    this.setState({
      currentPage: Number(event.target.id),
    });
  };

  handleCloseClick = (event) => {
    this.setState({
      modalVisible: false,
    });
    let body = document.getElementsByTagName("BODY")[0];
    body.style.overflow = "auto";
  };

  handleOpenModal = (event, project) => {
    console.log(project);
    this.setState({
      modalVisible: true,
      dataSelected: project,
    });
    let body = document.getElementsByTagName("BODY")[0];
    body.style.overflow = "hidden";
  };

  handleSelect = (selectedIndex, e) => {
    this.setState({ index: selectedIndex });
  };

  render() {
    const { projects, currentPage, todosPerPage, filter } = this.state;
    // Logic for displaying todos
    const indexOfLastTodo = currentPage * todosPerPage;
    const indexOfFirstTodo = indexOfLastTodo - todosPerPage;
    const projects_filter = projects.filter((item) => {
      if (filter !== "all") {
        return item.category === filter;
      } else {
        //Si es All retorno el item completo
        return item;
      }
    });
    const currentTodos = projects_filter.slice(
      indexOfFirstTodo,
      indexOfLastTodo
    );

    // Logic for displaying page numbers
    const pageNumbers = [];
    for (
      let i = 1;
      i <= Math.ceil(projects_filter.length / todosPerPage);
      i++
    ) {
      pageNumbers.push(i);
    }

    return (
      <Fragment>
        {this.state.modalVisible && (
          <ModalContainer>
            <Modal handleClick={this.handleCloseClick}>
              <div className="row ml-0 mr-0 mt-5">
                <div className="col-lg col-md col-12 v-center">
                  <h2 className="font-weight-bold text-uppercase color-black-opacity">
                    {this.state.dataSelected.name}
                  </h2>
                </div>
                <div className="col">
                  <p className="color-grey">
                    {this.state.dataSelected.description}
                  </p>
                </div>
              </div>
              <div className="row ml-0 mr-0 mt-5">
                <div className="col-lg col-md col-12">
                  <p className="v-center justify-content-start color-grey ">
                    <strong className="font-weight-bold text-uppercase color-black-opacity">
                      Categoria:
                    </strong>
                    <span
                      className={
                        "badge text-capitalize badge-pill badge-info ml-1 " +
                        "color-" +
                        this.state.dataSelected.category
                      }
                    >
                      {" " + this.state.dataSelected.category}
                    </span>
                  </p>
                </div>
                <div className="col-lg col-md col-12">
                  <p className="color-grey">
                    <strong className="font-weight-bold text-uppercase color-black-opacity">
                      Cliente:
                    </strong>
                    {" " + this.state.dataSelected.client}
                  </p>
                </div>
                <div className="col-lg col-md col-12">
                  <p className="color-grey">
                    <strong className="font-weight-bold text-uppercase color-black-opacity">
                      Trabajo:
                    </strong>
                    {" " + this.state.dataSelected.job}
                  </p>
                </div>
              </div>
              <div className="row ml-0 mr-0 justify-content-center">
                <div className="col-12 carrousel-images text-center mt-5 mb-5">
                  <Carousel
                    activeIndex={this.state.index}
                    onSelect={this.handleSelect}
                  >
                    {this.state.dataSelected.images.map((item, posicion) => {
                      return (
                        <Carousel.Item key={posicion}>
                          <img
                            className="d-block image-slider"
                            src={process.env.PUBLIC_URL + "/images/" + item.src}
                            alt={posicion}
                          />
                        </Carousel.Item>
                      );
                    })}
                  </Carousel>
                </div>
              </div>
            </Modal>
          </ModalContainer>
        )}
        <div className="container-fluid mt-5 p-5">
          <div className="row">
            <div className="col-12 mb-5">
              <h2 className="text-center text-black titulo">
                Proyectos Realizados
              </h2>
            </div>
            <div className="col-12 text-center">
              {this.state.category.map((item, posicion) => {
                return (
                  <Category
                    key={posicion}
                    name={item.name}
                    onFilter={this.onFilter}
                    filterSelected={this.state.filter}
                  />
                );
              })}
            </div>
            <div className="col text-center">
              <div className="row">
                {currentTodos
                  .filter((item) => {
                    if (this.state.filter !== "all") {
                      return item.category === this.state.filter;
                    } else {
                      //Si es All retorno el item completo
                      return item;
                    }
                  })
                  .map((item, posicion) => {
                    return (
                      <ItemProject
                        handleOpenModal={this.handleOpenModal}
                        key={posicion}
                        name={item.name}
                        description={item.description}
                        category={item.category}
                        job={item.job}
                        images={item.images}
                        client={item.client}
                      />
                    );
                  })}
              </div>
            </div>
            <div className="col-12 text-right">
              {this.state.currentPage - 1 > 0 && (
                <img
                  className="icon-arrow-atras mobile"
                  src={Arrow}
                  id={this.state.currentPage - 1}
                  onClick={this.handleClick}
                  alt="Icon arrow left"
                />
              )}
              {this.state.currentPage + 1 <= pageNumbers.length && (
                <img
                  className="icon-arrow mobile"
                  src={Arrow}
                  alt="Icon arrow right"
                  id={this.state.currentPage + 1}
                  onClick={this.handleClick}
                />
              )}
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default ListProyects;
