import React, { Fragment, useState, useEffect, useRef } from "react";
import logo from "../../logo-franklyn.png";
import "./navbar.css";

function Navbar(props) {
  const [colorNavbar, setColorNavbar] = useState("");
  const navbarRef = useRef();
  useEffect(() => {
    document.addEventListener("scroll", () => {
      const backgroundcolor =
        window.scrollY > navbarRef.current.clientHeight ? "navbar-scroll" : "";
      setColorNavbar(backgroundcolor);
    });
  });
  return (
    <Fragment>
      <nav
        id="navbar"
        className={"navbar navbar-expand-lg navbar-dark bg-dark " + colorNavbar}
        ref={navbarRef}
      >
        <a className="navbar-brand" href="/">
          <img className="logo" src={logo} width={100} alt="logo"></img>
        </a>
        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        <div
          className="collapse navbar-collapse justify-content-end"
          id="navbarSupportedContent"
        >
          <ul className="navbar-nav mr-5">
            <li className="nav-item active">
              <a className="nav-link" href="/">
                Inicio
                <span className="sr-only">(current)</span>
              </a>
            </li>
            <li className="nav-item">
              <a className="nav-link" href="/#">
                Sobre mi
              </a>
            </li>
            <li className="nav-item">
              <a className="nav-link" href="/#">
                Servicios
              </a>
            </li>
            <li className="nav-item">
              <a className="nav-link" href="/#">
                Proyectos
              </a>
            </li>
            <li className="nav-item">
              <a className="nav-link" href="/#">
                Contacto
              </a>
            </li>
          </ul>
        </div>
      </nav>
    </Fragment>
  );
}

export default Navbar;
